.checkout-form {
    border: 1px solid #ccc;
    padding-bottom: 25px;
}

.stepper-header {
    display: flex;
    justify-content: space-between;
}

.stepper-header>div {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}

.stepper-header>div>.stepper-circle {
    height: 25px;
    width: 25px;
    border: 1px solid;
    border-radius: 15px;
    /* background: black; */
    color: #fff;
    margin-top: 15px;
    font-size: 18px;
    z-index: 5;
}

.stepper-hr {
    border: 1px solid #ccc;
    position: absolute;
    left: 15%;
    width: 70%;
    top: 83px;
}

.checkout-field {
    padding-top: 25px;
    border: 0;
    padding: 7px 0;
}

.checkout-field>div {
    margin-bottom: 15px;
}

.input-field {
    border: 0px !important;
    border-bottom: 1px solid !important;
    border-left: 1px solid !important;
    border-radius: 0 !important;
    border-color: #CCC !important;
    box-shadow: none !important;
    transition: 0.4s !important;;
}

.add-effect:focus {
    border-bottom: 5px solid #337AB7 !important;
    border-left: 0 !important;
    transition: all ease-in-out .5s !important;
}

.ctrl-btn {
    margin-top: 10px;
}

.no-value {
    border: 3px solid red !important;
}

.fetch-cart-spinner {
    display: flex;
    justify-content: center;
    align-items: center;
}

.order-summary > h4 {
    font-weight: bold;
    margin-bottom: 0 !important;
    text-decoration: underline;
    text-underline-position: under;
}

.order-summary > ul {
    list-style-type: none;
    font-weight: bold;
    font-size: 16px;
}

.payfast-btn {
    margin-top: 15px;
}