.fetch-cart-spinner {
    display: flex;
    justify-content: center;
    align-items: center;
}

.cart-list {
    list-style-type: none;
    padding: 0 !important;
    margin-top: 15px;
}

.cart-header {
    display: flex;
    justify-content: space-between;
    padding: 14px;
    font-size: 20px;
    box-shadow: 0px 0px 0px 0px !important;
}

.cart-list>li {
    height: 55px;
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
    /* box-shadow: 0px 2px 2px 1px; */
    margin-bottom: 25px;
    border-radius:5px;
}

.cart-list>li>img {
    position: absolute;
    left: 13px;
    width: 55px;
    height: 55px;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
}

.item-div {
    position: absolute;
    left: 75px;
    margin-left: 15px;
}

.item-info {
    display: flex;
    text-align: center;
}

.item-info>a {
    width: 160px;
    white-space: nowrap;
    overflow-x: hidden;
}

.item-info>span {
    /* width: ; */
}

.item-info>a, .item-info>span {
    /* width: 50%; */
}

/* .item-info>span {
    justify-content: end;
} */

.item-qty {
    padding-left: 10px;
    padding-right: 10px;
}

.remove-item {
    position: absolute;
    right: 30px;
    margin-top: 17px;
}

.cart-main-btn {
    text-align: center;
    box-shadow: 0px 0px 0px 0px !important;
}

.cart-main-btn>button, .cart-main-btn>a {
    margin: 10px;
}

.empty-cart {
    outline: none !important;
}